<template>
  <div>
    <el-row>
      <el-col>
        <el-table :data="staffs" style="width: 100%">
          <el-table-column label="员工ID" prop="staffID"></el-table-column>
          <el-table-column label="员工名称" prop="staffName"></el-table-column>
          <el-table-column label="所属部门" prop="depName"></el-table-column>
          <el-table-column align="right">
            <!-- <template slot="header">
              <el-button size="small" type="primary" @click="dialogFormVisible1 = true">
                <i class="el-icon-circle-plus-outline" />添加员工
              </el-button>
            </template>-->
            <template slot-scope="scope">
              <el-button size="small" type="success" @click="upadte(scope.row)" round>
                <i class="el-icon-edit-outline" />修改
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
        ></el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Qs from "qs";

export default {
  inject: ["reload"],
  data() {
    return {
      currentPage: 1,
      total: 0,
      staffs: [],
    };
  },
  methods: {
    // getStaffList() {
    //   var table = { staffID: 0, staffName: "", depName: "" };
    //   var list = [];
    //   for (var i = 0; i < staffs.length; i++) {
    //     table.staffID = staffs[i].staffID;
    //     table.staffName = staffs[i].staffName;
    //     table.depName = this.getDepName(staffs[i].depID);
    //     list[i] = table;
    //   }
    //   this.staffList = list;
    //   console.log(staffList);
    // },
    upadte(row) {
      this.$router.push({
        name: "staffDetail",
        params: { staffID: row.staffID }
      });
    },
    handleCurrentChange(current) {
      this.currentPage = current;
      var api = "http://localhost:8888/staff/" + current + "/" + 5;
      this.axios
        .get(api)
        .then(response => {
          console.log("分页响应对象：" + response);
          this.staffs = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getData() {
      var api =
        "http://localhost:8888/staff/" +
        this.$route.params.current +
        "/" +
        this.$route.params.size;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.staffs = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    },
    // getDepName(depID) {
    //   var api = "http://localhost:8080/department/" + depID;
    //   this.axios
    //     .get(api)
    //     .then(response => {
    //       console.log(response);
    //       this.depName = response.data.depName;
    //     })
    //     .catch(error => {
    //       console.log(error);
    //     });
    // },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  created() {
    this.getData();
  },
  update() {
  }
};
</script>

